import React from 'react';
import { Helmet } from 'react-helmet';
import { Link, withPrefix } from 'gatsby';
import Header from '../components/header';
import Footer from '../components/footer';
import '../style.css';

const title = '東京・東日本橋のパーソナルジム【OXWM－オキシウム－】';
const description =
  'ダイエットからボディメイク・バルクアップも！理想の体に近づくマンツーマントレーニングを低価格で。DNA遺伝子検査でオリジナルトレーニング・食事メニューもご提案。';
const ogimage =
  'https://oxwm.net/wp-content/themes/oxwm/assets/img/common/ogimage.jpg';

export default function Layout({ children, page, subtitle, subtitleEn }) {
  const isTop = page === 'top';

  return (
    <>
      <Helmet>
        <html lang='ja' />
        <meta charSet='utf-8' />
        <title>{subtitle ? `${subtitle} | ${title}` : title}</title>
        <meta name='description' content={description} />
        <meta
          name='robots'
          content='max-snippet:-1, max-image-preview:large, max-video-preview:-1'
        />
        <link rel='canonical' href='https://oxwm.net/' />
        <meta property='og:site_name' content={title} />
        <meta property='og:type' content='website' />
        <meta property='og:title' content={title} />
        <meta property='og:description' content={description} />
        <meta property='og:url' content='https://oxwm.net/' />
        <meta property='og:image' content={ogimage} />
        <meta property='og:image:secure_url' content={ogimage} />
        <meta name='twitter:card' content='summary_large_image' />
        <meta name='twitter:domain' content='oxwm.net' />
        <meta name='twitter:title' content={title} />
        <meta name='twitter:description' content={description} />
        <meta name='twitter:image' content={ogimage} />
        <link rel='dns-prefetch' href='https://s.w.org/' />
        <meta name='viewport' content='width=device-width,initial-scale=1' />
        <meta name='format-detection' content='telephone=no' />
        {/* @TODO
    <script async="" src="./assets/js"></script>
    <script>
      window.dataLayer = window.dataLayer || [];
      function gtag() {
        dataLayer.push(arguments);
      }
      gtag('js', new Date());
      gtag('config', 'G-3KS46FC4XZ');
    </script>
    */}
        <script
          src='https://code.jquery.com/jquery-3.6.0.min.js'
          integrity='sha256-/xUj+3OJU5yExlq6GSYGSHk7tPXikynS7ogEvDej/m4='
          crossOrigin='anonymous'
          defer
        ></script>
        <script src={withPrefix('/plugin.js')} defer></script>
        <script src={withPrefix('/common.js')} defer></script>
        <script src={withPrefix('/wp-embed.min.js')} defer></script>
      </Helmet>
      {/* body */}
      <div className={`blog ${isTop ? 'home' : page}`} id='body'>
        <div id='l-container'>
          <Header page={page} subtitle={subtitle} />

          {isTop || (
            <div className='c-page-header'>
              <div className='c-page-header__inner'>
                <h1 className='c-page-header__title'>{subtitle}</h1>
                <div className='c-page-header__shoulder'>{subtitleEn}</div>
              </div>
            </div>
          )}

          {isTop || (
            <div className='p-breadcrumb'>
              <div className='p-breadcrumb__inner'>
                <p
                  className='p-breadcrumb__item'
                  itemScope=''
                  itemType='https://schema.org/BreadcrumbList'
                >
                  <span
                    itemProp='itemListElement'
                    itemScope=''
                    itemType='https://schema.org/ListItem'
                  >
                    <Link
                      itemType='https://schema.org/Thing'
                      itemProp='item'
                      to='/'
                      title='トップページへ移動'
                    >
                      <span itemProp='name'>TOP</span>
                    </Link>
                    <meta itemProp='position' content='1' />
                  </span>
                  <span className='p-breadcrumb__item__hr'> &gt; </span>
                  <span
                    itemProp='itemListElement'
                    itemScope=''
                    itemType='https://schema.org/ListItem'
                  >
                    <Link
                      itemType='https://schema.org/Thing'
                      itemProp='item'
                      to='/about/'
                      title='ジム情報へ移動'
                    >
                      <span itemProp='name'>{subtitle}</span>
                    </Link>
                    <meta itemProp='position' content='2' />
                  </span>
                </p>
              </div>
            </div>
          )}

          <div className='l-contents'>{children}</div>
          <Footer />
        </div>
        <div
          id='lightboxOverlay'
          className='lightboxOverlay'
          style={{ display: 'none' }}
        ></div>
        <div id='lightbox' className='lightbox' style={{ display: 'none' }}>
          <div className='lb-outerContainer'>
            <div className='lb-container'>
              <img
                className='lb-image'
                src='data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=='
                alt=''
              />
              <div className='lb-nav'>
                <Link className='lb-prev' to='/?test-prev'></Link>
                <Link className='lb-next' to='/?test-next'></Link>
              </div>
              <div className='lb-loader'>
                <a className='lb-cancel'></a>
              </div>
            </div>
          </div>
          <div className='lb-dataContainer'>
            <div className='lb-data'>
              <div className='lb-details'>
                <span className='lb-caption'></span>
                <span className='lb-number'></span>
              </div>
              <div className='lb-closeContainer'>
                <a className='lb-close'></a>
              </div>
            </div>
          </div>
        </div>
        <div className='l-overlay js-overlay'></div>
      </div>
    </>
  );
}
